export var getUsersByLogins = function (state, logins) { return fromUsers.getUsersByLogins(state.users, logins); };
export var getUsers = function (state) { return fromUsers.getUsers(state.users); };
export var isFavorite = function (state, componentKey) {
    return fromFavorites.isFavorite(state.favorites, componentKey);
};
export var getMarketplaceState = function (state) { return state.marketplace; };
