import * as api from '../../api/users';
export var RECEIVE_CURRENT_USER = 'RECEIVE_CURRENT_USER';
export var RECEIVE_USER = 'RECEIVE_USER';
export var SKIP_ONBOARDING = 'SKIP_ONBOARDING';
export var SET_HOMEPAGE = 'SET_HOMEPAGE';
export var receiveCurrentUser = function (user) { return ({
    type: RECEIVE_CURRENT_USER,
    user: user
}); };
export var receiveUser = function (user) { return ({
    type: RECEIVE_USER,
    user: user
}); };
export var skipOnboarding = function () { return ({ type: SKIP_ONBOARDING }); };
export var fetchCurrentUser = function () { return function (dispatch) {
    return api.getCurrentUser().then(function (user) {
        dispatch(receiveCurrentUser(user));
        return user;
    });
}; };
export var setHomePage = function (homepage) { return function (dispatch) {
    api.setHomePage(homepage).then(function () {
        dispatch({ type: SET_HOMEPAGE, homepage: homepage });
    }, function () { });
}; };
