export var CHANGE_VALUE = 'settingsPage/CHANGE_VALUE';
export var changeValue = function (key, value) { return ({
    type: CHANGE_VALUE,
    key: key,
    value: value
}); };
export var CANCEL_CHANGE = 'settingsPage/CANCEL_CHANGE';
export var cancelChange = function (key) { return ({
    type: CANCEL_CHANGE,
    key: key
}); };
