import { combineReducers } from 'redux';
import users, * as fromUsers from './users/users';
import groups, * as fromGroups from './groups/groups';
import loading, * as fromLoading from './loading';
import query from './query';
import filter from './filter';
import selectedPermission from './selectedPermission';
import error from './error';
export default combineReducers({
    users: users,
    groups: groups,
    loading: loading,
    query: query,
    filter: filter,
    selectedPermission: selectedPermission,
    error: error
});
export var getUsers = function (state) { return fromUsers.getUsers(state.users); };
export var getGroups = function (state) { return fromGroups.getGroups(state.groups); };
export var isLoading = function (state) { return fromLoading.isLoading(state.loading); };
export var getQuery = function (state) { return state.query; };
export var getFilter = function (state) { return state.filter; };
export var getSelectedPermission = function (state) { return state.selectedPermission; };
export var getError = function (state) { return state.error; };
