// @flow
import { keyBy, sortBy, uniqBy } from 'lodash';
import { RECEIVE_DEFINITIONS } from './actions';
import { DEFAULT_CATEGORY, getCategoryName } from '../../utils';
/*:: import type { Definition } from '../../types'; */
/*::
type State = { [key: string]: Definition };
*/
/*::
type Action = { type: string, definitions: Definition[] };
*/
var reducer = function (state /*: State */, action /*: Action */) {
    if (state === void 0) { state /*: State */ = {}; }
    if (action.type === RECEIVE_DEFINITIONS) {
        return keyBy(action.definitions, 'key');
    }
    return state;
};
export default reducer;
export function getDefinition(state /*: State */, key /*: string */) {
    return state[key];
}
export function getAllDefinitions(state /*: State */) {
    return Object.keys(state).map(function (key) { return state[key]; });
}
export var getDefinitionsForCategory = function (state /*: State */, category /*: string */) {
    return getAllDefinitions(state).filter(function (definition) { return definition.category.toLowerCase() === category.toLowerCase(); });
};
export var getAllCategories = function (state /*: State */) {
    return uniqBy(getAllDefinitions(state).map(function (definition) { return definition.category; }), function (category) {
        return category.toLowerCase();
    });
};
export var getDefaultCategory = function (state /*: State */) {
    var categories = getAllCategories(state);
    if (categories.includes(DEFAULT_CATEGORY)) {
        return DEFAULT_CATEGORY;
    }
    else {
        var sortedCategories = sortBy(categories, function (category) {
            return getCategoryName(category).toLowerCase();
        });
        return sortedCategories[0];
    }
};
